import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage,MenuController ,NavParams, NavController } from 'ionic-angular';

import { Tab1Root, Tab3Root, Tab4Root , Tab5Root } from '../';

@IonicPage()
@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html'
})
export class TabsPage {
  tab1Root: any = Tab1Root;
  //tab3Root: any = Tab3Root;
  public uno;
  public dos;
  tab1Title = " ";
  tab2Title = " ";
  tab3Title = " ";
  ico1="home";
  ico2="contact";
  constructor(public navParams: NavParams,public menu: MenuController,public navCtrl: NavController, public translateService: TranslateService) {
    this.menu.enable(true);
    translateService.get(['TAB1_TITLE', 'TAB3_TITLE', 'TAB4_TITLE', 'TAB5_TITLE']).subscribe(values => {
      this.uno = navParams.get("uno");
     // this.dos = navParams.get("dos");
      if(this.uno=='Tab1Root' || this.uno==undefined)
      {
        this.tab1Title = values['TAB1_TITLE'];
        this.tab1Root=Tab1Root;
       this.ico1="home";
        this.ico2="contact";
      }
      else
      {
        if(this.uno=='Tab3Root' )
        {
          this.tab1Title = values['TAB3_TITLE'];
          this.ico1="contact";
          this.tab1Root=Tab3Root;
          }
        else
        {
          if(this.uno=='Tab4Root' )
          {
            this.tab1Title = 'Lluvia de Ideas';
            this.ico1="home";
            this.tab1Root=Tab4Root;
           }
          else
          {
            this.tab1Title = 'Eventos';
            this.ico1="home";
            this.tab1Root=Tab5Root;
        
          }
         
        }
   
      }

     
    });
  }
  ionViewDidLoad() {
    //this.tab1Root=this.uno;
   // this.tab3Root=this.dos;
  }
}



